
#footer_wrap {
    display: flex;
    justify-content: space-between;
}
#footer_links {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 5px;
}
#footer_links a {
    color: white;
}
#job_links {
    display: flex;
    align-items: left;
    flex-direction: column;
    justify-content: space-around;
}
#job_links a {
    color: white;
}
#logo1 {
    width: 7.5%;
    border-radius: 50%;
    margin-left:25px;
}
#logo2 {
    width: 7.5%;
    border-radius: 15%;
    margin-left:25px;
}