@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:ital,wght@1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#logo {
    max-width: 25%;
    max-height: 25%;
    position: fixed !important;
    display: block;
    border-radius: 1em;
    box-shadow: 0 0 12px rgba(33,33,33,1); 
    opacity: 90%;
}
#header_wrap {
    padding: 10px;
}
#navigation {
    width: 20%;
    background-color: #EAF2F8;
    margin-top: 7.5%;
    margin-left:2.5%;
    padding: 0;
    position: fixed;
    height: 30%;
    font-size: 125%;
    overflow: auto;
    border-radius: 1em;
    box-shadow: 0 0 12px rgba(33,33,33,1); 
    opacity: 90%;
}
#navigation ul {
    list-style-type: none;
}
#navigation li a {
    display: flex;
    flex-direction: column;
    color: #000;
    padding: 8px 16px;
    text-decoration: none;
    font-weight: bold;
}
#navigation li a.active {
    background-color: #2471A3;
    color: white;
}
#navigation li a:hover:not(.active) {
    background-color: #555;
    color: white;
}
.little_icons img{
    width: 5%;
    border-radius: 50%;
    margin: 0px 5px;
}
#doggo {
    width: 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5%;
}
#doggo a {
    color:white;
    font-size: 1.5em;
}
* {
    margin: 0px;
    padding: 0px;
}
#full_body_wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin-left:20%;
}
#image_wrap {
    display: flex;
    flex-direction: column;
    flex-wrap:wrap;
    align-items: center;
    justify-self: center;
    padding: 10px;
    margin: 10px;
    width: 70%;
    border: 2px solid black;
    border-radius: 1em;
    transition: box-shadow .3s;
    background-color: white;
}
#image_wrap:hover {
    box-shadow: 0 0 12px rgba(33,33,33,1); 
}

.text_wrap {
    margin:10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    font-family: 'Noto Sans JP', sans-serif;
}
.text_wrap a {
    color:blue;
    font-weight: bold;
    font-family: 'Source Serif Pro', serif;
}
#line_break {
    width: 100%;
}
img {
    width:100%;
    height:auto;
}
h3 {
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
}
h2{
    font-family: 'Roboto', sans-serif;
    padding: 10px;
}
h1 {
    text-align: center;
    padding: 10px;
    font-family: 'Roboto', sans-serif;
}
p{
    font-family: 'Ubuntu', sans-serif;
}
#HeadShotImg {
    border: 1px solid black;
    border-radius: 50%;
    width:25%;
    margin: 10px;
}
#resume_button {
    border-radius: 1em;
    height: 3em;
    padding: 5px;
    background-color: #2471A3;
    color: white;
    transition: box-shadow .3s;
    font-weight: bold;
}
#resume_button:hover {
    box-shadow: 0 0 12px rgba(27, 27, 27, 0.4); 
}
.full_page_wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin-left:20%;
}

.contact_page_wrapper {
    border: 2px solid black;
    border-radius: 1em;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 65%;
    transition: box-shadow .3s;
    background-color: white;
}
.contact_page_wrapper:hover {
    box-shadow: 0 0 12px rgba(33,33,33,1); 
}
#form_wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
}
.google_maps {
    color: black;
    overflow: hidden;
    text-align: center;
}
#boldMe {
    font-weight: bold;
    font-size: 1.05em;
}

#main_page_h1 {
    margin-left:20%;
    color: white;
    text-shadow: 0 0 12px rgba(33,33,33,1);
}

@media only screen and (max-width: 720px) {
    #image_wrap {
        width: 80%;
    }
}
#background {
    background-image: url(/static/media/Background1.39867423.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
}
#CoffeeShopBG {
    background-image: url(/static/media/coffeeBG.95e720a5.jpeg);
    background-repeat: no-repeat;
    background-attachment: fixed;
}
#SWBG {
    background-image: url(/static/media/SWBG.669ca4fa.jpeg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 55%;
}
#EventBG {
    background-image: url(/static/media/eventBG.e2932c7c.jpeg);
    
    background-repeat: no-repeat;
    background-attachment: fixed;
}

#footer_wrap {
    display: flex;
    justify-content: space-between;
}
#footer_links {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 5px;
}
#footer_links a {
    color: white;
}
#job_links {
    display: flex;
    align-items: left;
    flex-direction: column;
    justify-content: space-around;
}
#job_links a {
    color: white;
}
#logo1 {
    width: 7.5%;
    border-radius: 50%;
    margin-left:25px;
}
#logo2 {
    width: 7.5%;
    border-radius: 15%;
    margin-left:25px;
}
